.text-centered {
  text-align: center;
}
.species-information-font {
  font-weight: 600;
  font-size: 16px;
  margin-left: 3px;
  vertical-align: middle;
}
.red-rectangle {
  width: 20px;
  height: 30px;
  background: red;
}
.green-rectangle {
  width: 20px;
  height: 30px;
  background: green;
}
.yellow-rectangle {
  width: 20px;
  height: 30px;
  background: yellow;
}
.grey-rectangle {
  width: 20px;
  height: 30px;
  background: grey;
}
.trend-image {
  width: 25px;
  height: auto;
}
.table-fixed thead {
  width: 97%;
}
.table-fixed tbody {
  height: 230px;
  overflow-y: auto;
  width: 100%;
}
.color-red {
  color: red;
}
.color-green {
  color: green;
}
.color-yellow {
  color: yellow;
}
.color-grey {
