  border-color: #23a5c8;
}
.panel-species-info .panel-heading {
  background: #23a5c8;
  border-color: #23a5c8;
}
.arrow-big {
  font-size: 20px;
}
.table.table-font-big th {
  font-size: 16px;
}
.i-box-mimi {
  margin-top: 20px;
}
.input-font-mimi-normal {
  font-size: 18px;
}
.input-font-mimi-big {
  font-size: 20px;
}
.font-mimi-big {
  font-size: 20px;
}
.sk-spinner-chasing-dots.sk-spinner {
  width: 30px;
  height: 30px;
}
.new-theme-icon {
  background-position: 0 0;
}
.icon-phylum {
  background-image: url(phylum.png) !important;
}
.icon-class {
  background-image: url(class.png) !important;
}
.icon-order {
  background-image: url(order.png) !important;
}
.icon-family {
  background-image: url(family.png) !important;
}
.mimi-table-cell-font {
  font-size: 18px;
}
